import React from 'react';
import me from '../../assets/pictures/workingAtComputer.jpg';
import meNow from '../../assets/pictures/currentme.jpg';
import { Link } from 'react-router-dom';
import ResumeDownload from './ResumeDownload';

export interface AboutProps { }

const About: React.FC<AboutProps> = (props) => {
    return (
        // add on resize listener
        <div className="site-page-content">
            {/* <img src={me} style={styles.topImage} alt="" /> */}
            <h1 style={{ marginLeft: -16 }}>Welcome</h1>
            <h3>I'm Nasser Alsunaid</h3>
            <br />
            <div className="text-block">
                <p>
                    I'm a co-op trainee currently working at Riyad Bank! In July
                    of 2025 I officially graduate from Prince Sultan University
                    with a BS in Computer Science.
                </p>
                <br />
                <p>
                    Thank you for taking the time to check out my portfolio. I
                    really hope you enjoy exploring it as much as I did. If you have any questions or comments, feel
                    free to contact me using{' '}
                    <Link to="/contact">this form</Link> or shoot me an email at{' '}
                    <a href="mailto:nfalsunaid@gmail.com">
                        nfalsunaid@gmail.com
                    </a>
                </p>
            </div>
            <ResumeDownload />
            <div className="text-block">
                <h3>About Me</h3>
                <br />
                <p>
                    From a young age, Ive been drawn to strategywhether it was
                    trying to outsmart games like <b>chess</b> or figuring out how to make money as a kid.
                    Ive always liked dissecting how things work, not just technically
                    but conceptually too. That mindset naturally pulled me toward tech
                    and problem-solving.
                    <br />
                    <br />
                    I actually started university as an Information Systems major because
                    it blended business and techtwo areas Im deeply interested in. But lets
                    just say... my memory didnt get the memo. I eventually switched to Computer
                    Science when I realized I preferred courses that focused on understanding
                    concepts over memorizing facts. CS challenged me in a way that clicked
                    and Ive been exploring the intersection of tech, strategy, and innovation ever since.
                </p>
                <br />
                <div className="captioned-image">
                    <img src={me} style={styles.image} alt="" />
                    <p>
                        <sub>
                            <b>Figure 1:</b> Baby Nasser :)
                        </sub>
                    </p>
                </div>

                <p>
                    After high school, I had no idea what I wanted to do. I knew I didnt want a
                    memorization-heavy major (my memory and I dont exactly get along), so I
                    looked for something that blended business and techInformation Systems seemed
                    like the perfect fit. But after two years, I realized I liked every Computer
                    Science course I took and hated the only IS course I had. So, I made the switch,
                    and honestly, havent looked back.
                </p>
                <br />
                <p>
                    During my time at Prince Sultan University, I got to work on some great
                    projectsmost notably my senior project, where my team (Abdulmalik Alghailan,
                    Meshari Alhumali, and Saud Alrabiah) and I built a glove that translates sign
                    language into speech. We were supervised by Dr. Wadii Boulila, and it was
                    easily one of the highlights of my academic journey.
                </p>
                <br />
                <p>
                    Im currently working on something new that I hope will make a meaningful
                    impact. It's still in its early stages, but Ill just say thisits something
                    that blends my love for culture, aesthetics, and technology. Im designing
                    both the business and the website myself, and while I wont give away too
                    much yet, lets just say if you're into fashion and discovering things from
                    all over the world... youll probably want to stick around.
                </p>
                <br />
                <br />
                <div style={{}}>
                    <div
                        style={{
                            flex: 1,
                            textAlign: 'justify',
                            alignSelf: 'center',
                            flexDirection: 'column',
                        }}
                    >
                        <h3>My Hobbies</h3>
                        <br />
                        <p>
                            Lets start with video games. I havent picked up a controller in a while,
                            but I still talk like Im deep in the scene. These days, Im more into
                            strategy gameschess especially. I play regularly, and while I wouldnt
                            call myself a grandmaster (or even close), I enjoy the challenge and the
                            way it pushes me to think a few steps ahead. That love for logic, planning,
                            and world-building never really left meit just evolved.
                        </p>
                        <br />
                        <p>
                            Fashion is something Ive always been drawn to. I like staying up to date with
                            new trends, exploring how brands from different cultures express identity,
                            and just observing how style evolves over time. I'm not trying to become a
                            fashion critic (yet), but its something I enjoy learning about, and its
                            definitely influencing the direction of my current project.
                        </p>
                        <br />
                        <div className="captioned-image">
                        <img src={meNow} style={styles.image} alt="" />
                        <p>
                            <sub>
                                <b>Figure 2:</b> Me, April 2025
                            </sub>
                        </p>
                    </div>
                        <p>
                            As for TV shows, thats where I get really into it. Fantasy, action,
                            supernatural elementsthose are my sweet spots. I love complex worlds, magical
                            systems, elves, and anything that lets my imagination run wild. Shows like
                            that have influenced me so much, Im even considering writing a book just for
                            fun. Nothing seriousjust me seeing if I can take all the stories, patterns,
                            and character arcs Ive absorbed over the years and turn them into something
                            of my own.
                        </p>
                        <br />
                        <p>
                            Im super grateful for my family and friends whove always supported me
                            whether its helping me stay grounded or just letting me rant about the latest
                            episode of whatever Im obsessed with. If you've made it this far, thanks for
                            reading and getting to know me a bit better!
                        </p>
                    </div>
                    
                </div>
                <br />
                <br />
                <p>
                    Thanks for reading about me! If you ever want to chat about fashion, games, TV, or anything in between, you
                    can reach out through the <Link to="/contact">contact page</Link> or hit me up
                    on{' '}
                    <a href="https://www.linkedin.com/in/nasseralsunaid" target="_blank" rel="noreferrer">
                        LinkedIn
                    </a>
                    .
                </p>
                <br />
            </div>
        </div>
    );
};

const styles: StyleSheetCSS = {
    contentHeader: {
        marginBottom: 16,
        fontSize: 48,
    },
    image: {
        height: 'auto',
        width: '100%',
    },
    topImage: {
        height: 'auto',
        width: '100%',
        marginBottom: 32,
    },
};

export default About;
